html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.uw-brand-color {
  color: #4b2e83
}

.shs-brand-color {
  color: #18453b;
}

.frc-brand-color {
  color: #0066B3;
}

.aruw-brand-color {
  color: #4f4f4f;
}

.google-blue {
  color: #4285f4;
}

.google-green {
  color: #0f9d58;
}

.google-yellow {
  color: #f4b400;
}

.google-red {
  color: #db4437;
}

.nav-icon {
  height: 2rem;
  margin-bottom: 0.25rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.nav-home-link {
  color: #8ec5f1;
}

.nav-home-link:hover {
  color: #8ec5f1;
  text-decoration: none;
}

.n-hero-image {
  border-radius: 150px;
  width: 200px;
  height: 200px;
  box-shadow: 0px 0px 3px 1px #ccc;
  object-fit: cover;
}

.n-overlay {
  z-index: 0;
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}

.container {
  position: relative;
  text-align: left;
}

.n-card {
  margin: 20px;
}

.centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.n-card-image {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
  object-fit: cover;
}

.n-card-test {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  object-fit: cover;
  position: absolute;
  bottom: 0px;
}

.n-card-info {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 8px;
  padding-bottom: 10px;
  min-height: 100px;
  width: 300px;
  position: absolute;
  bottom: 0px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: #ffffffde;
  color: #212529;
}

.n-card-title {
  font-size: 24px;
  margin: 0px;
  color: #212529;
}

.n-card-subtitle {
  font-size: 16px;
  margin-top: 1em;
  color: #212529;
}

.n-slide-viewer {
  height: inherit;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
  position: relative;
  overflow: visible;
}

.n-slide-desktop {
  height: calc(100vh - 3.5rem);
}

.n-slide-mobile {
  height: calc(85vh - 3.5rem);
}

.n-slide {
  height: inherit;
  width: inherit;
  position: absolute;
}

.n-slide-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: inherit;
}

.n-slide-card {
  background-color: #ffffffde;
  min-width: 30vw;
  max-width: 20rem;
  display: block;
  
  position: absolute;
  right: 2rem;
  bottom: 4rem;

  margin-left: 2rem;

  padding: 1rem;
}

.n-slide-card-mobile {
  background-image: linear-gradient(#ffffffb0, #ffffff);

  min-width: 100vw;
  display: block;
  
  position: absolute;
  bottom: 0;

  padding: 1rem;
}

.n-slide-card-buttons {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}

.n-slide-card-button {
  margin-right: 1rem;
}

.n-slide-info {
  overflow: auto;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.n-slide-title {
  font-size: 32px;
  margin: 0px;
  color: #212529;
}

.n-slide-body {
  font-size: 18px;
  margin: 0px;
  color: #212529;
  overflow: hidden;
}

.n-slide-title-mobile {
  font-size: 28px;
  margin: 0px;
  color: #212529;
}

.n-slide-body-mobile {
  font-size: 16px;
  margin: 0px;
  color: #212529;
  overflow: hidden;
}

.n-slides-control {
  width: 100%;
  text-align: center;
  bottom: 1rem;
  position: absolute;
}

.n-dot {
  height: 12px;
  width: 12px;
  margin: 5px;
  background-color: #eee;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
}

.n-dot.active {
  background-color: #8ec5f1;
}

li.nostyle {
  list-style-type: none;
}

.box {
  display: flex;
  flex-flow: column;
  height: 100%;
  width: 100%;
}

.n-footer {
  justify-content: space-between;
  margin: 30px;
}

.n-carousel {
  padding: 5rem;
}

.n-project-container {
  min-width: 50vw;
  max-width: 90vw;
  padding: 2rem;
  margin-bottom: 2rem;

  
  box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
}

.n-project-image {
  width: 200px;
  height: 200px;
  border-radius: 50px;
  box-shadow: 0px 0px 3px 1px #ccc;
  object-fit: cover;
}

.n-project-text {
  flex-grow: 3 !important;
}

.n-project-left {
  justify-content: center;
  display: block;
}

.n-project-button {
  width: 200px;
  display: block;
  margin-top: 1rem;
}

.n-icon {
  height: 1em;
  margin: 0 0.4em 0.2em 0.1em;
}

.n-span {
  margin-right: 1rem;
}

.n-footer {
  justify-content: space-between;
}

.n-social-icon {
  opacity: 0.6;
  transition: 0.3s;
}

.n-social-icon:hover {
  opacity: 1;
}